import { ProductInfo } from "@/dto/ProductInfo";
import { FirestoreService } from "@/services/FirestoreService";
import { Component, Vue, Watch } from "vue-property-decorator";

import VueSilentbox from "vue-silentbox";

import SellerDetails from "@/dto/SellerDetails";
import { CartService } from "@/services/CartService";
import { PlatformDetails } from "@/dto/PlatformDetails";
import { PlatformInfo } from "@/dto/PlatformInfo";
import { pl } from "date-fns/locale";

@Component({
  components: {
    VueSilentbox
  }
})
export default class Products extends Vue {
  registerError = "";
  showError = false;
  email = "";
  password = "";
  repeatedPassword = "";
  loading = false;
  rating = 5;
  detailsLoading = true;
  productInfo = new ProductInfo();
  otherSellersList: SellerDetails[] = [];
  productDesc = "";
  alignments: any[] = ["start", "center", "end"];

  // images: any[] = [
  //   {
  //     src: "https://images.indianexpress.com/2020/09/PUBG-mobile-1.jpg",
  //     description: "Sunken dreams II. by Arbebuk",
  //     thumbnail: "https://images.indianexpress.com/2020/09/PUBG-mobile-1.jpg",
  //     thumbnailWidth: "150px"
  //   },
  //   {
  //     src: "https://buff.game/wp-content/uploads/2020/11/PUBG.png",
  //     description: "Sunken dreams II. by Arbebuk",
  //     thumbnail: "https://images.indianexpress.com/2020/09/PUBG-mobile-1.jpg",
  //     thumbnailWidth: "150px"
  //   },
  //   {
  //     src: "https://i.gadgets360cdn.com/large/pubg_mobile_1575547086878.jpg",
  //     description: "Sunken dreams II. by Arbebuk",
  //     thumbnail: "https://images.indianexpress.com/2020/09/PUBG-mobile-1.jpg",
  //     thumbnailWidth: "150px"
  //   },
  //   {
  //     src: "https://www.youtube.com/watch?v=z2HE_the-A0&t=2s",
  //     thumbnail: "https://images.indianexpress.com/2020/09/PUBG-mobile-1.jpg",
  //     description: "Tunnel View Sunrise by Porbital",
  //     thumbnailWidth: "150px"
  //   }
  // ];
  genres: string[] = ["Action", "Adventure", "FPS/TPS"];
  worksOn: string[] = ["Windows", "Mac"];
  gameDetails: string[] = [
    "Rates 18+ (Mature)",
    "Single-player",
    "Multiplayer",
    "Third-Person",
    "First-Person"
  ];

  developerInfo: any[] = [
    { itemName: "Release date:", value: "June 10, 2015" },
    { itemName: "Developers", value: "Rockstar Games" },
    { itemName: "Publisher", value: "Rockstar Games" }
  ];
  sysReq: any[] = [
    { itemName: "Release date:", value: "June 10, 2015" },
    { itemName: "Developers", value: "Rockstar Games" },
    { itemName: "Publisher", value: "Rockstar Games" }
  ];
  platfornInfo: any[] = [
    {
      title: "Global",
      description: "Can't be activated in India",
      link: "Check Restrictions"
    },
    // {
    //   title: "Global",
    //   description: "Can't be activated in India",
    //   link: "Check Restrictions"
    // },
    // {
    //   title: "Global",
    //   description: "Can't be activated in India",
    //   link: "Check Restrictions"
    // },
    {
      title: "Other",
      description:
        "Refer to the product description to find activation instructions."
    },
    {
      title: "Global",
      description: "Can't be activated in India",
      link: "Check Restrictions"
    }
  ];
  $silentbox: any;

  async mounted() {
    await this.fetchData();
  }
  private async fetchData() {
    try {
      this.productInfo = await FirestoreService.getSingleDocument(
        "products",
        this.$route.params.id
      );

      if (this.productInfo.sellerDetails.length > 1) {
        this.otherSellersList = this.productInfo.sellerDetails.slice(1);
      }
      this.detailsLoading = false;

      if (this.productInfo.preOrder) {
        const releaseDate = new PlatformDetails();
        releaseDate.id = "relase-date";
        releaseDate.rank = 99999;
        const platformInfo = new PlatformInfo();
        platformInfo.id = "release-date-info";
        platformInfo.name = "Release Date";
        platformInfo.description = this.productInfo.preOrderDate;
        platformInfo.logoUrl = "https://firebasestorage.googleapis.com/v0/b/gamer-x-codes.appspot.com/o/SITE%2FCalendar_icon_2.svg?alt=media&token=9188cb3f-dfd0-4ca0-af62-04635bebc5be";
        releaseDate.platformInfo = platformInfo;
        this.productInfo.platformDetails.push(releaseDate);
      }
    } catch (error) {
      Vue.toasted.show(error.message, { icon: "cross", type: "error" });
    }
  }

  openOverlay(image: any, index: number) {
    console.log(image)
    this.$silentbox.open(image);
    //(this.$refs.silentbox as Vue & { openOverlay: (image:any, index:number) => boolean }).openOverlay(image, index);
  }
  @Watch('$route', { deep: true, })
  async onRouteChanged(value: boolean, oldValue: boolean) {
    await this.fetchData();
  }
  async addToCart(sellerDetails: SellerDetails) {
    try {
      await CartService.add(sellerDetails, this.productInfo);
    } catch (error) {
      Vue.toasted.show(error, { icon: "cross", type: "error" });

    }
  }
  async buyNow(sellerDetails: SellerDetails) {
    try {
      await CartService.add(sellerDetails, this.productInfo);
      this.$router.push("/cart?buy=true");

    } catch (error) {
      Vue.toasted.show(error, { icon: "cross", type: "error" });

    }
  }
}
